s inside the following class wrap\n      //       to test this fix for wrappers in stacked UiBoxes\n      // :not(.UiBoxImage):not(.UiBox_stack) & {}\n        & {     flex-basis: 72rem !important }\n        &_mid { flex-basis: 54rem !important }\n        &_min { flex-basis: 36rem !important }\n        &_tiny{ flex-basis: 23rem !important }\n    }\n  }\n}\n\n%wraps {\n  max-width: 100%;\n  box-sizing: border-box;\n  & & { padding: 0 }\n}","//////////////////////////////\n// Default Variables\n//////////////////////////////\n$Breakpoint-Settings: (\n  'default media': all,\n  'default feature': min-width,\n  'default pair': width,\n\n  'force all media type': false,\n  'to ems': false,\n  'transform resolutions': true,\n\n  'no queries': false,\n  'no query fallbacks': false,\n\n  'base font size': 16px,\n\n  'legacy syntax': false\n);\n\n$breakpoint: () !default;\n\n//////////////////////////////\n// Imports\n//////////////////////////////\n@import \"breakpoint/settings\";\n@import 'breakpoint/context';\n@import 'breakpoint/helpers';\n@import 'breakpoint/parsers';\n@import 'breakpoint/no-query';\n\n@import 'breakpoint/respond-to';\n\n@import \"breakpoint/legacy-settings\";\n\n//////////////////////////////\n// Breakpoint Mixin\n//////////////////////////////\n\n@mixin breakpoint($query, $no-query: false) {\n  @include legacy-settings-warning;\n\n  // Reset contexts\n  @include private-breakpoint-reset-contexts();\n\n  $breakpoint: breakpoint($query, false);\n\n  $query-string: map-get($breakpoint, 'query');\n  $query-fallback: map-get($breakpoint, 'fallback');\n\n  $private-breakpoint-context-holder: map-get($breakpoint, 'context holder') !global;\n  $private-breakpoint-query-count: map-get($breakpoint, 'query count') !global;\n\n  // Allow for an as-needed override or usage of no query fallback.\n  @if $no-query != false {\n    $query-fallback: $no-query;\n  }\n\n  @if $query-fallback != false {\n    $context-setter: private-breakpoint-set-context('no-query', $query-fallback);\n  }\n\n  // Print Out Query String\n  @if not breakpoint-get('no queries') {\n    @media #{$query-string} {\n      @content;\n    }\n  }\n\n  @if breakpoint-get('no query fallbacks') != false or breakpoint-get('no queries') == true {\n\n    $type: type-of(breakpoint-get('no query fallbacks'));\n    $print: false;\n\n    @if ($type == 'bool') {\n      $print: true;\n    }\n    @else if ($type == 'string') {\n      @if $query-fallback == breakpoint-get('no query fallbacks') {\n        $print: true;\n      }\n    }\n    @else if ($type == 'list') {\n      @each $wrapper in breakpoint-get('no query fallbacks') {\n        @if $query-fallback == $wrapper {\n          $print: true;\n        }\n      }\n    }\n\n    // Write Fallback\n    @if ($query-fallback != false) and ($print == true) {\n      $type-fallback: type-of($query-fallback);\n\n      @if ($type-fallback != 'bool') {\n        #{$query-fallback} & {\n          @content;\n        }\n      }\n      @else {\n        @content;\n      }\n    }\n  }\n\n  @include private-breakpoint-reset-contexts();\n}\n\n\n@mixin mq($query, $no-query: false) {\n  @include breakpoint($query, $no-query) {\n    @content;\n  }\n}\n","\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n@import \"~@/styles/theme/breaks\";\n@import \"~@/styles/theme/colors\";\n@import \"~@/styles/extend/wrap\";\n\n@mixin DualToneIcon {\n  /deep/ .icon--text path,\n  /deep/ .icon--ornament--rule {\n    fill: Color(alt) !important;\n  }\n  /deep/ .icon--ornament--rinceau {\n    fill: Color(dark) !important;\n  }\n  /deep/ .icon--text {\n    filter: none !important;\n  }\n}\n\n#AppNav {\n  & {\n    & { // outer layout\n      position: sticky;\n      // position: fixed;\n      z-index: 10;\n      top: 0;\n      left: 0;\n      width: 100vw;\n      height: 6rem;\n    }\n    & { // inner layout\n      display: flex;\n      justify-content: space-between;\n      align-items: center;\n      padding: 1.5rem;\n    }\n    user-select: none;\n  }\n  & { // theme\n\n    color: inherit;\n    &.light { color: Color(slate) }\n    &.cream { color: Color(dark)  }\n    &.dark  { color: Color(light) }\n    &.rust  { color: Color(light) }\n\n    &.cream, &.light {\n      @include DualToneIcon;\n    }\n  }\n  & { // transition\n    transition: color .2s 0s ease-in-out;\n    transition-delay: 0s !important;\n    transition-duration: .3s !important;\n    transition-timing-function: ease-in-out !important;\n  }\n  &-menu {\n    --c: #{Color(theme)};\n    color: var(--c);\n  }\n  &.rust #{&}-menu {\n    --c: #{Color(silver)};\n  }\n  &-logo {\n    >svg {\n      pointer-events: none;\n    }\n    @include Break( min-width Breaks(4) ){\n      align-self: flex-start !important;\n    }\n    @include Break( max-width Breaks(4) ){\n      >svg:only-child { width: 5rem !important }\n    }\n  }\n  &-menuJoin {\n    display: inline-block;\n    margin: 0 .6em 0 0;\n    padding: 0 .6em 1px .7em;\n    line-height: 1.5;\n    font-size: .88em;\n    font-weight: 600;\n    font-variant: small-caps;\n    text-decoration: none;\n    text-transform: lowercase;\n    letter-spacing: .05em;\n    color: var(--c);\n    border: 2px solid var(--c);\n    border-radius: 6em;\n    &.open {\n      display: none;\n    }\n  }\n  @include Break( max-width Breaks(4) ){\n    padding: 0 1em;\n    height: 4rem;\n    background: rgba(250, 250, 250, .96);\n    color: Color(slate);\n    box-shadow: -4px 0 2rem 4px rgba(48, 41, 3, 0.08);\n    transition: .4s ease-out;\n    @include DualToneIcon;\n    &.light {\n      background-color: rgba( Color(light), .88);\n      color: Color(dark);\n      #AppNav-menu { --c: #{Color(theme)} }\n    }\n    &.cream {\n      background-color: rgba( Color(cream), .88);\n      color: Color(slate);\n      #AppNav-menu { --c: #{Color(theme)} }\n    }\n    &.dark  {\n      background: rgba( Color(light), .88);\n      color: Color(slate);\n      #AppNav-menu { --c: #{Color(theme)} }\n    }\n    &.rust  {\n      background-color: rgba( Color(alt), .88);\n      color: Color(light);\n      #AppNav-menu { --c: #{Color(light)} }\n    }\n  }\n}\n","$c-black: #0e0e0e;\n$c-matte: invert($c-black,99.5%);\n$c-theme: #BA9454;\n$c-alt:   #D21034;\n\n$colors:(\n  dark:    $c-black, // #0E0E0E\n  slate:   lighten($c-black,15%), // #323232,\n  silver:  lighten($c-black,67%), // #B8B8B8\n  cream:   lighten(desaturate(adjust-hue($c-theme,7deg),14),42), // #F5F3ED,\n  creamDark: #BFB4A0,\n  light:   mix( white, $c-matte, 69% ), // #F6F4EE\n  matte:   $c-matte, // #F0F0F0\n  theme:   $c-theme, // #BA9454\n  alt:     $c-alt,\n);\n\n@function Color($key) {\n  $color: map-get($colors,$key);\n  @return $color;\n}"],"sourceRoot":""}]);

